.navbar-style {
    width: 100%;
    height: 75px;
    position: fixed;
    top: 0;
    z-index: 1;
    display: flex;
    background-color: #00bfff;
    /* box-shadow: 0 2px 2px -2px rgba(0, 0, 0, .5) */
    transition-timing-function: ease-in-out;
    transition: 0.5s;
}

.navbar-style__invis {
    background-color: transparent;
}

.navbar-style .name {
    flex: 30% 1;
    display: flex;
    justify-content: left;
    align-items: center;
}

.navbar-style .content {
    flex: 50% 1;
    display: flex;
    justify-content: right;
    align-items: center;

}

.navbar-style .name .name-style{
    margin-left: 1rem;
    margin-right: 0.5rem;
    color: white;
    font-family: Ubuntu-B;

}

.navbar-style .name .icon-style{
    padding-top: 5px;
    color: white;
    margin-left: 0.5rem;
    transition: 0.3s;
}

.navbar-style .name .icon-style:hover{
    color: #F52F57;
}

.navbar-style .content .link-styles a{
    text-decoration: none;
    margin-right: 20px;
    font-size: 25px;
    color: white;
    font-family: Ubuntu-M;

}

.navbar-style .content .link-styles a:hover{
    color: #F52F57;
    cursor: pointer;
    transition: 0.3s;
}
