.cardP {
    margin: 45px auto 20px;
    padding-bottom: 45px;
    border-radius: 16px;
    width: 85%;
    height: 1%;
    background-color: #272838;
    overflow: hidden;
    opacity: 90%;
    box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
}

.cardBodyP {
    padding: 1rem;
}

.projectCard{
    margin: 45px 25px 0;
    /* margin-left: 50px; */
    float: left;
    border-radius: 16px;
    height: 600px;
    /* width: 340px; */
    width: 30%;
    transition: 0.5s ease;
    box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
}

.projectCard:nth-child(1){
    background-color: #555555;
   
}

.projectCard:nth-child(2){
    background-color: #6FD397;
}

.projectCard:nth-child(3){
    background-color: #7289DA;
}

.cardHeaderP {
    font-size: 25px;
    color: white;
    opacity: 100%;
    position: relative;

}

.projectCardHeader {
    margin: 10px;
    font-size: 25px;
    color: white;
    opacity: 100%;
    text-align: center;
    font-family: Ubuntu-M;
}

.projectCardDesc {
    margin: 10px;
    /* font-size: 1rem; */
    font-size: 1vw;
