.projectCard:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
    box-shadow: 5px 5px 18px white;
}

.projectCard:nth-child(1):hover{
    /* background: linear-gradient(#555555, #F58F7C); */
    /* background: linear-gradient(gray, #F52F57); */
    /* background: linear-gradient(#3f87a6, #F69D3C); */
    background: linear-gradient(gray, #F69D3C);
}
.projectCard:nth-child(2):hover{
    background: linear-gradient(#6FD397, #00bfff);
}
.projectCard:nth-child(3):hover{
    background: linear-gradient(#7289DA, #e66465);
}

.projectCard:hover .iconP{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

